.roadMapContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-size: cover;
  color: rgb(255, 255, 255);
  background-repeat: no-repeat;
}

.progress {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.heading {
  font-size: 74px;
  font-weight: 700;
  filter: drop-shadow(10px 10px 8px #000000);
}

.roadMapText {
  font-size: 28px;
  font-weight: 200;
  filter: drop-shadow(4px 3px 2px #000000);
}

@media screen and (min-width: 1000px) {
  .roadMapText {
    font-size: 44px;
    font-weight: 700;
    filter: drop-shadow(10px 6px 6px #000000);
  }
}

.roadMapBG {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  width: 100%;
  background: rgba(0, 25, 250, 0.5);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 30px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.roadMapBG2 {
  display: flex;
  width: 100%;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: column;
  background: rgba(255, 0, 0, 0.5);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 30px;
  margin: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}
